.one-law__link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.one-law__header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
}
.one-law__face {
  max-width: 70px;
  margin-right: 5px;
}
.one-law__img {
  width: 100%;
}
.one-law__stats {
  flex-basis: 100%;
  flex-shrink: 1;
  flex-grow: 1;
  align-self: center;
}
.one-law__results {
  display: none;
  width: 100%;
  justify-content: space-around;
}
.one-law__result--red {
  color: #f06b48;
}
.one-law__result--gray {
  color: #555;
}
.one-law__result--green {
  color: #46d16e;
}
.one-law__body {
  margin-bottom: 20px;
}
.one-law__title {
  color: #4b5a6e;
  font-weight: 400;
  margin-bottom: 10px;
}
.one-law__text {
  color: #555;
  font-weight: 300;
  display: block;
}
.one-law__text--bold {
  font-weight: 400;
}
.one-law__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.one-law__half {
  margin-bottom: 5px;
}
.one-law__half--grades {
  display: flex;
  justify-content: flex-end;
}
.one-law__half--comms {
  margin-right: 20px;
}
.one-law__grades {
  border: 2px solid #acacac;
  border-radius: 3px;
  align-self: center;
  text-align: right;
  padding: 6px 10px;
  padding-left: 30px;
  position: relative;
}
.one-law__grades:before {
  left: 5px;
}
.one-law__grades--green {
  border-color: #46d16e;
  color: #46d16e;
  margin-right: 10px;
}
.one-law__grades--green:before {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
}
.one-law__grades--red {
  border-color: #f06b48;
  color: #f06b48;
}
.one-law__grades--red:before {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
}
.one-law__grade {
  padding-left: 5px;
}
.one-law__grade:before {
  padding-right: 1px;
}
.one-law__grade--red {
  color: #f06b48;
}
.one-law__grade--red:before {
  content: '-';
}
.one-law__grade--green {
  color: #555;
}
.one-law__grade--green {
  color: #46d16e;
}
.one-law__grade--green:before {
  content: '+';
}
@media (min-width: 980px) {
  .one-law {
    flex-shrink: 1;
    flex-basis: calc(50% - 10px);
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .one-law__stats {
    display: flex;
    flex-wrap: wrap;
    align-self: stretch;
    flex-direction: column;
    justify-content: space-around;
    min-height: 50px;
  }
  .one-law__results {
    display: flex;
  }
}
.status-bar {
  position: relative;
  background-color: #fbfbfb;
  width: 100%;
}
.status-bar__bg {
  height: 18px;
  background: linear-gradient(115deg, #46d16e 0%, #46d16e 25%, #70db8f 25%, #70db8f 75%, #46d16e 75%, #46d16e 100%);
  background-size: 35px 100%;
  background-repeat: repeat;
}
.doer {
  width: 100%;
  background-color: #fff;
  padding: 10px;
  margin-bottom: 50px;
}
.doer__face {
  overflow: hidden;
  margin-bottom: 23px;
  text-align: center;
}
.doer__img {
  width: 100%;
  max-width: 450px;
}
.doer__block {
  margin-bottom: 23px;
}
.doer__text {
  color: #555;
}
.doer__desc .doer__text {
  margin-bottom: 23px;
}
.doer__title {
  font-family: 'yefimov', sans-serif;
  font-weight: 400;
  margin-bottom: 5px;
}
@media (min-width: 980px) {
  .doer {
    border: 1px solid #e8e8e8;
    display: flex;
    max-width: 1200px;
    margin: 0 auto 50px;
  }
  .doer__face {
    width: 270px;
    margin-right: 25px;
    flex-shrink: 0;
  }
  .doer__info {
    flex-basis: 100%;
    flex-shrink: 1;
  }
}
.confirmation {
  background-color: #f06b48;
  padding: 9px 10px;
  margin-left: -10px;
  margin-right: -10px;
  display: none;
}
.confirmation--show {
  display: block;
}
.confirmation__text {
  color: #fff;
  text-align: center;
}
.confirmation__link {
  text-decoration: underline;
}
@media (min-width: 980px) {
  .confirmation {
    flex-basis: 100%;
    flex-shrink: 1;
    align-self: center;
    margin-left: 0;
    margin-right: 0;
  }
}
.upload-btn {
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: translateY(100%);
  transition: transform 0.2s;
}
.upload-btn:hover {
  transform: translateY(0);
}
.upload-btn__label {
  display: block;
  background-color: rgba(33,33,33,0.5);
  cursor: pointer;
  padding: 12px 0;
  position: relative;
  text-align: center;
}
.upload-btn__label:before {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 16px 16px;
  width: 16px;
  height: 16px;
  left: 20px;
  display: inline-block;
  content: '';
  margin-right: 16px;
  vertical-align: middle;
}
.upload-btn__input {
  display: none;
}
.upload-btn__text {
  color: #fff;
  vertical-align: middle;
}
.profile__img:hover + .upload-btn,
.profile__no-img:hover + .upload-btn {
  transform: translateY(0);
}
.profile {
  padding: 10px;
  margin-bottom: 35px;
}
.profile__face {
  position: relative;
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  margin-bottom: 50px;
}
.profile__img,
.profile__no-img {
  width: 100%;
}
.profile__face--no-photo .profile__img {
  display: none;
}
.profile__no-img {
  display: none;
}
.profile__face--no-photo .profile__no-img {
  display: inline-block;
}
.profile__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.profile__name {
  color: #5ea4c5;
  padding-right: 10px;
  margin-bottom: 15px;
}
.profile__edit-btn {
  color: #5ea4c5;
  position: relative;
  font-size: 0;
}
.profile__edit-btn:before {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 16px 16px;
  width: 16px;
  height: 16px;
  display: inline-block;
  content: '';
}
.profile__info {
  margin-bottom: 15px;
  display: flex;
  flex-wrap: wrap;
}
.profile__footer {
  display: flex;
  flex-wrap: wrap;
}
.profile__text {
  text-align: center;
  margin-bottom: 40px;
  width: 100%;
}
@media (min-width: 980px) {
  .profile {
    display: flex;
    padding: 0;
  }
  .profile__face {
    margin: 0;
    margin-right: 30px;
    width: 265px;
    flex-shrink: 0;
  }
  .profile__desc {
    flex-basis: 100%;
    flex-shrink: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .profile__header {
    width: 100%;
  }
  .profile__name {
    margin-bottom: 0;
  }
  .profile__edit-btn {
    font-size: 0.875rem;
    padding-left: 25px;
  }
  .profile__edit-btn:before {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .profile__info {
    margin-bottom: 0;
  }
  .profile__footer {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
  .profile__text {
    width: auto;
    text-align: left;
    margin-bottom: 0;
  }
}
.info {
  display: flex;
  flex-shrink: 1;
  min-width: 0;
  padding-right: 5px;
  flex-grow: 1;
}
.info__left {
  flex-shrink: 0;
  width: 100px;
}
.info__right {
  flex-basis: 100%;
  flex-shrink: 1;
  min-width: 0;
}
.info__item {
  margin-bottom: 15px;
}
.info__left .info__item {
  color: #acacac;
}
@media (min-width: 980px) {
  .info {
    width: 50%;
  }
}
.gang {
  flex-basis: 50%;
  text-align: center;
  margin-bottom: 30px;
}
.gang__face {
  display: inline-block;
  height: 40px;
  vertical-align: middle;
}
.gang__img {
  max-height: 100%;
  max-width: 100%;
}
.gang__percent {
  display: inline-block;
  margin-left: 10px;
  vertical-align: middle;
  font-weight: 600;
}
@media (min-width: 980px) {
  .gang {
    flex-basis: auto;
    margin-bottom: 0;
  }
}
.loader {
  height: 20px;
  background: linear-gradient(115deg, #f06b48 0%, #f06b48 25%, #f17a5a 25%, #f17a5a 75%, #f06b48 75%, #f06b48 100%);
  background-size: 35px 100%;
  background-repeat: repeat;
  animation: bgCrawl 2s linear infinite;
}
.crop {
  margin-bottom: 10px;
  text-align: center;
}
@media (min-width: 980px) {
  .crop {
    margin-left: 50px;
    margin-right: 50px;
  }
}
.croppie-container {
  padding: 0;
}
.croppie-container .cr-boundary {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}
.croppie-container .cr-slider {
  width: 100%;
}
.croppie-container .cr-slider-wrap {
  margin: 0;
  width: 100%;
  margin-bottom: 20px;
}
.klichko {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  width: 100px;
  height: 100%;
}
.klichko__img {
  position: absolute;
  left: 100%;
  transform-origin: left bottom;
  width: 300px;
  transform: rotate(0);
  transition: transform 0.3s;
  bottom: 0;
}
.klichko__perl {
  position: absolute;
  left: 100px;
  width: 300px;
  background-color: #fff;
  transition: 0.3s;
  bottom: 600px;
  border: 1px solid #000;
  padding: 5px 10px;
}
.klichko:hover {
  background-color: rgba(0,0,0,0.1);
}
.klichko:hover .klichko__img {
  transform: rotate(-45deg);
}
.klichko:hover .klichko__perl {
  left: -400px;
  bottom: 350px;
}
@media (min-width: 980px) {
  .klichko {
    display: block;
  }
}
